<template>
    <div  class="m-5">
        <div class="card">
            <div class="card-header">
                <step-progress size="small" :steps="mySteps" :current-step="active" icon-class="fa fa-check"
                active-color="green" active-thickness=2 passive-thickness=2 line-thickness=4></step-progress>
            </div>

            <div class="card-body">
                <div v-if="active == 0">
                    <v-text-field :label="$t('search')" hide-details v-model="search" class="m-5" color="#8ac5e3">
                        <template
                            v-if="$vuetify.breakpoint.mdAndUp"
                            v-slot:append-outer
                        >
                            <v-btn class="mt-n2" elevation="1" fab small>
                            <v-icon>el-icon-search</v-icon>
                            </v-btn>

                        </template>
                    </v-text-field>

                    <v-row class="mt-6">
                        <v-col cols="12" sm="4" v-for="item in all_services" :key="item.id">
                            <Cardvue v-bind:item="item"/>
                        </v-col>
                    </v-row>
                </div>
                <div v-if="active == 1">
                    <h1> 01</h1>
                </div>
                <div v-if="active == 2">
                    <h1> Facture</h1>
                </div>
                <div v-if="active == 3">
                    <h1> New</h1>
                </div>
            </div>

            <div class="card-footer">
                <el-button style="margin-top: 12px;" @click="prev">Precedent</el-button>
                <el-button style="margin-top: 12px;" @click="next">Suivant</el-button>
            </div>
        </div>
    </div>
</template>
<script>""

    import Cardvue from '@/components/dashboard/orders/CardProduct.vue';

    import {FormWizard, TabContent} from 'vue-step-wizard'
    import "vue-step-wizard/dist/vue-step-wizard.css";

    import StepProgress from 'vue-step-progress';
    // import the css (OPTIONAL - you can provide your own design)
    import 'vue-step-progress/dist/main.css';
    import axios from 'axios';
    // import moment from 'moment';
    import NProgress from 'nprogress';

    export default {

        name: "BasicStepper",

        components: {
            Cardvue, FormWizard, TabContent,
            'step-progress': StepProgress
        },
        
        data() {
              return {
                active: 0,
                search: '',
                show: false,
                all_services : {},
                mySteps: ['Choisir le(s) service(s)', 'Produits', 'Facture'],
            }
        },

        watch: {
            search : function() {
                NProgress.start();
                if (this.search == '' || this.search == ' ') {
                this.all_services = this.services_response;
                NProgress.done();
                }

                else {
                this.all_services = this.services_response.filter(data => !this.search || data.nom.toLowerCase().includes(this.search.toLowerCase()));
                NProgress.done();
                }

            },

            commande_services : function() {
                console.log(this.commande_services);
            }

            // searchUser : function() {

            //   NProgress.start();
            //   this.searchCustomers();

            // }

        },
        
        created() {
            this.getInitData();
        },

        methods: {
            next() {
                if (this.active++ > 2) this.active = 0;
            },
            prev() {
                if (this.active-- < 0) this.active = 0;
            },
            async getInitData()
            {
                NProgress.start();
                console.log("this.all_services");
                const response = await axios.get('xpress/service/getAll');
                this.services_response = response.data.services;
                this.all_services = response.data.services;
                console.log(this.all_services);
                NProgress.done();
            },
        }
        
    }
</script>

<style>
  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 200px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }

  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
</style>